<template>
  <div class="index">
    <div class="master" :key="index" v-for="(item, index) in ansList">
      <a-avatar class="master-avatar" shape="square" icon="reddit" size="large" />
      <a-input-search :value="item.problem" placeholder="Say something" class="ask" :disabled="loading" :loading="loading" @pressEnter="goSearch"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ChatGPT',
  components: { },
  props: { },
  data () {
    return {
      loading: false,
      ansList: [
        {problem: '', answer: ''},
        {problem: '', answer: ''},
        {problem: '', answer: ''},
        {problem: '', answer: ''},
        {problem: '', answer: ''},
        {problem: '', answer: ''},
        {problem: '', answer: ''}
      ]
    }
  },
  watch: { },
  computed: { },
  methods: {
    goSearch (e) {
      console.log(e)
      this.loading = true
    }
  },
  created () { },
  mounted () { }
}
</script>
<style scoped>
.index {
  border: 1px solid rgba(236, 240, 241, 1);
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
  border-radius: 5px;
  padding: 20px 0;
  min-height: 50vh;
}
.ask {
  max-width: 800px;
  min-width: 300px;
}
.master {
  margin-bottom: 10px;
}
.master-avatar {
  margin: 5px 10px;
}
</style>

<style>
.ask .ant-input {
  height: 60px !important;
  font-size: 20px;
}
</style>



